import React, { useEffect, useState } from 'react';
import { FaTooth } from 'react-icons/fa';

export default function Admin() {
    const jwToken = localStorage.getItem('jwToken')

    const handleLogout = (event) => {
        event.preventDefault();
        localStorage.removeItem("jwToken")
        window.location.href = 'http://localhost:3000/luxsmile#/login'
    }

    useEffect(() => {
        if (jwToken) {
            // Trimitem tokenul către server
            fetch('http://localhost:8080/stayConnected', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ jwToken }) // Corectare: body, nu budy
            })
            .then(response => response.json())
            .then(data => {
                if(data.message === 'jwToken este invalid') {
                    window.location.href = 'http://localhost:3000/luxsmile#/login'
                }
            })
            .catch(error => {
                console.error('Eroare la trimiterea tokenului la server:', error);
            });
        } else {
            console.log('Nu există token în localStorage.');
            window.location.href = 'http://localhost:3000/luxsmile#/login'
        }
    }, [])
  
    return (
        <div className='admin'>
            {/* Log out */}
            {/* <button onClick={handleLogout}>Log out</button> */}
            <header className='logo'>
                <div className='boxLogo'>
                    <FaTooth className='icon' />
                    <h1>LuxSmile</h1>
                </div>
                <button onClick={handleLogout}>Log out</button>
            </header>
            <main>
                <div className='tools'>
                    <button className='tool'>Personal</button>
                    <button className='subcategory'>Administrație</button>
                    <button className='subcategory'>Medicii Stomatologi</button>
                    <button className='subcategory'>Tehnicieni Dentari</button>
                    <button className='subcategory'>Asistenți Medicali</button>
                </div>
                <div className='datas'>
gfdg
                </div>
            </main>
        </div>
    );
}
